import {
  FC,
  KeyboardEvent,
  PropsWithChildren,
  ReactNode,
  useEffect,
  useRef
} from "react";

import {
  SearchOutline,
  CloseOutline,
} from 'antd-mobile-icons';

export interface SearchBarProps extends PropsWithChildren {
  // 输入框value
  value?: string;
  // 是否显示清除按钮
  clearable?: true;
  // 搜索图标
  icon?: ReactNode;
  // placeholder
  placeholder?: string;
  // extra class name
  className?: string;
  // input 有焦点时触发
  onFocus?: () => void;
  // 搜索
  onSearch?: (value: string) => void;
  // 清理
  onClear?: () => void;
}

const SearchBar: FC<SearchBarProps> = ({
  value,
  clearable,
  icon,
  placeholder,
  className,
  children,
  onFocus,
  onClear,
  onSearch
}) => {

  const inputRef = useRef<HTMLInputElement | null>(null);

  className ??= '';
  icon ??= <SearchOutline />;

  // 键盘事件
  const onKeyDown = (e: KeyboardEvent<HTMLInputElement>) => {
    if(e.code === 'Enter' || e.keyCode === 13) {
      onSearch?.(inputRef.current?.value ?? '');
    }
  };

  // 清除输入
  onClear ??= () => {
    if(!inputRef.current) {
      return;
    }
    inputRef.current.value = '';
  };

  useEffect(() => {
    if(!inputRef.current) {
      return;
    }
    inputRef.current.value = value ?? '';
  }, [value]);

  return (
    <div className={`search-bar ${className}`}>
      {icon}
      <input
        ref={e => inputRef.current = e}
        placeholder={placeholder}
        className='flex-grow'
        onKeyDown={onKeyDown}
        onFocus={onFocus}/>
      {
        clearable && <div className='btn p-0' onClick={onClear}>
          <CloseOutline/>
        </div>
      }
      {
        children
      }
    </div>
  );
};

export default SearchBar;